.plus-icon {
  width: 12px;
  position: relative;
  height: 12px;
  overflow: hidden;
  flex-shrink: 0;
}
.button,
.property-1icon-left {
  display: flex;
  align-items: center;
  justify-content: center;
}
.button {
  width: 50px;
  position: relative;
  line-height: 140%;
  height: 14px;
  flex-shrink: 0;
}
.property-1icon-left {
  width: 98px;
  border-radius: var(--br-9xs);
  background-color: var(--primary-blue);
  height: 29px;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-base);
  box-sizing: border-box;
  gap: var(--gap-9xs);
  text-align: center;
  font-size: var(--body-text-2-reg-size);
  color: var(--grey-100);
  font-family: var(--body-text-2-reg);
}
