.email {
  position: relative;
  line-height: 140%;
  font-weight: 900;
}
.left-child {
  width: 1px;
  position: relative;
  border-right: 1px solid var(--color-black);
  box-sizing: border-box;
  height: 18px;
  display: none;
}
.left1 {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.window-close-icon {
  width: 16px;
  position: relative;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.property-1default {
  width: 266px;
  border-radius: var(--br-9xs);
  background-color: var(--grey-50);
  height: 42px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-4xs);
  box-sizing: border-box;
  gap: var(--gap-178xl);
  text-align: left;
  font-size: var(--body-text-2-reg-size);
  color: var(--grey-700);
  font-family: var(--body-s);
}
